import { ref } from "vue";
import { Service } from "@/services/service";
import { storageFactory } from "./storage";
import { initiateLinkageCodes } from "@/apps/HTS/composables/linkageCode";

type MetaDataKey =
  | "villages"
  | "traditional_authorities"
  | "districts"
  | "regions"
  | "locations";
type MetadataStruct = { key: string; endpoint: string };

const supportedPrograms = ["ITS"];
const storage = storageFactory("IndexedDBStorage");
const retry = ref(0);


const OFFLINE_METADATA: MetadataStruct[] = [
  {
    key: "villages",
    endpoint: "villages",
  },
  {
    key: "traditional_authorities",
    endpoint: "traditional_authorities",
  },
  {
    key: "districts",
    endpoint: "districts",
  },
  {
    key: "regions",
    endpoint: "regions",
  },
  {
    key: "locations",
    endpoint: "locations",
  },
];

/** get offline metadata from local storage
 *
 * @param key the key of the metadata to fetch
 * @returns the metadata
 * @returns empty array if no metadata with provided key is found
 */
export async function getOfflineMetadata(key: MetaDataKey) {
  try {
    const data = await storage.getFromStorage(key);
    if (data) return data;
    return [];
  } catch (err) {
    console.log(err);
    return [];
  }
}

/** refresh offline metadata from the API
 * @returns void
 * */
export function refreshOfflineMetadata() {
  OFFLINE_METADATA.forEach(async (meta: MetadataStruct) => {
    // delete from local storage
    storage.deleteFromStorage(meta.key);
  });
  syncOfflineMetadata();
}

/** fetch data from the API necessay for offline functionality
 * @returns void
 */
export async function syncOfflineMetadata() {
  try {
    await initiateLinkageCodes()
  } catch (e) {
    console.warn(e)
  }
  for (const meta of OFFLINE_METADATA) {
    if (!(await storage.existsinStorage(meta.key))) {
      try {
        const data = await Service.getJson(meta.endpoint, { paginate: false });
        storage.saveToStorage(meta.key, data);
      } catch (e) {
        console.warn(`Issue loading/saving metadata ${meta}: ${e}`)
      }
    }
  }
}
